<style lang='scss' scoped>
.stepper-container{
    display: flex;
    align-items: center;
    .substract{
          background-image: url("data:image/png;base64,");
      }
    .add{
        background-image: url("http://s3plus.meituan.net/v1/mss_e2821d7f0cfe4ac1bf9202ecf9590e67/cdn-prod/file:9096d347/318c525df8eba9c557594305c44b2ddf.png");
    }
    .step-btn{
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 50%;
        display: inline-block;
    }
}
</style>
<template>
<div class="stepper-container">
    <span class="substract step-btn" v-show="value>0" @click="substractClick()"></span>
    <span class="text" v-show="value>0">{{value}}</span>
    <span class="add step-btn" @click="addClick()"></span>
</div>
</template>

<script>
    export default {
        name: "stepper",
        props:{
            value:{
                type:Number,
                default:0
            }
        },
        model:{
            prop:"value",
            event:"value-changed"
        },
        data(){
            return{
                // value:0,
            }
        },
        methods:{
            substractClick(){
                this.$emit("value-changed",this.value-1)
            },
            addClick(){
                this.$emit("value-changed",this.value+1)
            }
        }
    }
</script>